.card.card--template {
    width: 200px;
    display: inline-block;
    margin: 15px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
}
.card.card--template .icon i {
    font-size: 20px;
}
.card.card--template .icon {
    line-height: normal;
    width: auto;
    height: auto;
    text-align: right;
    align-self: right;
    padding: 5px 12px;
    border-radius: 100%;
    color: #fff;
    z-index: 2;
}
.card.card--template .icon:hover {
    background: rgba(255, 255, 255, 0.2);
}
.card label {
    position: static;
    z-index: 2;
    display: flex;
    color: #fff;
    padding: 10px;
    flex-direction: row;
    align-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    justify-content: space-between;
}
.card.card--template figure {
    background: rgba(61, 107, 162, 0.3);
    opacity: 1;
}
.card.card--template .info {
    text-shadow: 0 0 4px #2a2a2a;
}
.card.card--template .info .name {
    position: static;
    font-size: 18px;
}
.card.card--template .info .version {
    right: 0;
    left: 10px;
    bottom: 10px;
    padding-top: 100px;
}
.card.card--template .info .input {
    position: absolute;
    right: 10px;
    bottom: 10px;
    z-index: 3;
}
.dark .card.card--template .card-selected {
    background-image: url(/dashboard/static/img/dashboard/03.png);
    background-position: center;
    background-size: cover;
}
.card.card--template .card-selected:hover figure {
    z-index: -1;
}

